<template>
    <div class="percentRingBox">
		<div class="ringBox">
			<img src="@/assets/page1/ringLayer2x.png" class="ringLayer" alt="">
			<span class="percentValue" :style="{color}">{{content3}}%</span>
			<svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="100%" height="100%" viewBox="0 0 160 160" class="ringSvg" style="position: absolute">
				<circle cx="80" cy="80" r="70" stroke-width="20" fill="none" :stroke="color" class="ringCircle" :style="{strokeDashoffset:content2}"></circle>
			</svg>
		</div>
		<div class="textBox">
			<div class="divideBar">
				<img src="@/assets/page1/Group1321314656.png" alt="" style="width: 100%; height: 100%">
			</div>
			<span class="ringValue">{{content4}}</span>
			<span class="ringName">{{content1}}</span>
		</div>
	</div>
</template>

<style lang="less" scoped>
.percentRingBox {
	height: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;

	.ringBox {
		height: 100%;
		aspect-ratio: 1/1;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		margin-right: 4.62%;
		
		.ringLayer {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		.percentValue {
			color: #fff;
			font-size: 100%;
			height: 25%;
			line-height: 100%;
			vertical-align: top;
			text-shadow: 0px 0px 9px rgba(255,208,59,0.7);
			font-family: 'titleFont', sans-serif;
			text-shadow: 0px 0px 10% rgba(255,208,59,0.7);
		}

		.ringSvg {
			position: absolute;
		}

		.ringCircle {
			stroke-dasharray: 439.82; /*2πr, r=70*/
			stroke-linecap: round;
			transform: scaleX(-1);
			transform-origin: 50% 50%;
		}

	}
	.textBox {
		width: 49.13%;
		height: 66.25%;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		
		
		.devideBar {
			width: 100%;
			height: 22.64%;
		}

		.ringName {
			position: absolute;
			bottom: 0;
			right: 0;
			color: rgba(255, 255, 255, 0.7);
			font-family: 'pingfangSc', sans-serif;
			font-size: 63.64%;
			height: 41.51%;
			line-height: 150%;
		}
		.ringValue {
			position: absolute;
			top: 0;
			right: 0;
			font-family: 'titleFont', sans-serif;
			height: 50.94%;
			font-size: 100%;
			line-height: 150%;
			background: linear-gradient(180deg, #FFFFFF 42%, #FFD03B 100%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
}

</style>

<script>
export default {
	props:{
		itemName:{
			type: String
		},
		percent:{
			type: Number
		},
		ringColor:{
			type: String
		},
		events:{
			type: String
		}
	},
	data(){
		return {
			content1: '',
			content2: 0,
			content3: 0,
			content4: '',
			color: '',
		}
	},
	mounted(){
		this.content1 = this.itemName;
		let percentage = parseInt(this.percent);
		this.content2 = 439.82 - 439.82*percentage/100; 
		this.content3 = percentage;
		this.content4 = this.events;
		this.color = this.ringColor;
		return false;
	}
}
</script>
